
@import url('https://fonts.googleapis.com/css2?family=League+Script&display=swap');

body {
  font-size: 1rem;
  text-align: center;
  margin: 0;
  text-align: center;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nav{

}
.navUl{
  background-color: white;
  padding: 0;
  display: flex;
}
.navLink{
  color: black;
}
.header{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}
.date{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.datepicker{
  width: 4rem;
}
h1{
  font-size: 4rem;
  font-family: 'League Script', cursive;
  
}
.form{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}
.inputitem{
  height: 1.8rem;
  width: 15rem;
  margin: 0.5rem 0.5rem;
  font-weight: bolder;
}
.inputquantity{
  height: 1.5rem;
  width: 2rem;
  margin: 0.5rem 0.5rem;
  
}
.submit{
  height: 2.5rem;
  width: 6rem;
  font-size: 1.1rem;
  border-radius: 30px;
  color: white;
  background-color: rgba(140, 121, 154, 1);
  margin-bottom: 1rem;
  
  
}
.container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

ul{
  width: 100%;
  padding: 2rem 25rem;
  background-color: rgb(156, 68, 130, 0.8);
}
.box{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid white;
  border-radius: 15px;
  margin: 0.5rem 0;
  width: 100%;
  height: 5rem;
  background-color: rgba(199, 134, 179, 0.8);
  cursor: pointer;
}
li{
  padding: 2rem 2rem;
  list-style: none;
  align-items: center;

}

p{
  font-size: 1.5rem;
  justify-content: center;
  color: rgb(243, 236, 236);
  text-transform: capitalize;
}
.quantity{
  color: rgb(112, 57, 151)
}
button{
 
  height: 2rem;
  cursor: pointer;
  font-size: 1.5rem;
  border-radius: 5px;
  justify-content: flex-end;
  color: rgb(243, 217, 217);
  background-color: rgba(73, 50, 66, 0.8);
  margin: 0 1rem 0 0;
}

.Total{
  display: grid;
  grid-template-columns: 25rem 25rem;
  
}


@media only screen and (min-device-width:  280px) and (max-device-width:  767px){
  body{font-size: 0.8rem;margin: 1rem;}
  .Total{grid-template-columns: 5rem 5rem;}
  .total{font-size: 1.2rem;}
  ul{padding: 1rem;}

}

@media only screen and (min-device-width:  768px) and (max-device-width:  1365px){
  body{font-size: 1.2rem;}
}

@media only screen and (min-device-width: 1900px) {
  body{font-size: 1.5rem;}
} 